/* eslint-env browser */
import React, { Component } from 'react';
import { Button } from 'react-bootstrap';
import classNames from 'classnames';
import $ from 'jquery';
import S from 'string';
import '../../css/imageManager.scss';

/**
 * TinyMCEReactRedux
 */
export default class ImageManager extends Component {
  constructor(props) {
    super(props);

    this.bindedHandleInsertFormSubmitMessage = this.handleInsertFormSubmitMessage.bind(this);

    this.state = {
      image: null,
      size: 'normal',
      clickAction: 'none',
      clickRequired: false,
    };
  }

  componentDidMount() {
    window.addEventListener('message', this.bindedHandleInsertFormSubmitMessage, false);
  }

  componentWillUnmount() {
    window.removeEventListener('message', this.bindedHandleFormSubmitMessage);
  }

  getFormData($form) {
    const ar = $form.serializeArray();
    const params = {};
    $.map(ar, (n) => { params[n.name] = n.value; });
    return params;
  }

  handleChangeForm(e) {
    const target = e.target;
    const name = target.name;
    if (target.type === 'checkbox') {
      this.setState({ [name]: target.checked });
    } else {
      const value = target.value;
      const state = {};
      if (name === 'clickAction' && value === 'none') {
        state.clickRequired = false;
      }
      state[name] = value;
      this.setState(state);
    }
  }

  handleUploadFiles(e) {
    const { uploadImagesFunc } = this.props;
    e.preventDefault();
    if (S(this.imageFileListEl.value).isEmpty()) {
      alert('');
      return;
    }
    const formEl = e.target;
    const formData = new FormData(this.uploadFormEl);
    uploadImagesFunc(formData);
    formEl.reset();
  }

  handleInsertFormSubmitMessage(e) {
    if (e.origin !== location.origin) {
      alert('');
      return;
    }
    if (e.data.type !== 'submitInsertForm') return;

    if (this.state.image === null) {
      alert('');
      return;
    }
    const params = this.state;
    this.props.insertImageFunc({ params });
  }

  handleChangeUploadFiles(e) {
    const files = e.target.files;
    for (let i = 0, len = files.length; i < len; i++) {
      const file = files[i];
      if (file.size > 1024 * 1024 * 3) {
        e.target.value = null;
        alert(`${file.name}3MB`);
        return;
      }
    }
  }

  handleClickDelete(image) {
    this.props.deleteImageFunc(image);
  }

  handleClickImage(image) {
    this.setState({ image });
  }

  submitInsertForm() {
    const param = this.getFormData($(this.insertFormEl));
    window.parent.postMessage({ type: 'insertImage', value: JSON.stringify(param) }, '*');
  }

  render() {
    const imageList = this.props.imageList;
    const image = this.state.image;
    return (
      <div>
        <form ref={(el) => { this.uploadFormEl = el; }} className="upload-form" onSubmit={e => this.handleUploadFiles(e)}>
          <fieldset className="container-fluid">
            <legend></legend>
            <div className="row">
              <div className="col-sm-12">
                <br />
                3MB<br />
                (JPG, GIF, PNG)
              </div>
            </div>
            <div className="row">
              <div className="col-sm-12">
                <input ref={(el) => { this.imageFileListEl = el; }} name="imageFileList" type="file" onChange={e => this.handleChangeUploadFiles(e)} accept="image/jpeg,image/gif,image/png" multiple />
                <Button type="submit"></Button>
              </div>
            </div>
          </fieldset>
        </form>

        <form ref={(el) => { this.insertFormEl = el; }} onChange={e => this.handleChangeForm(e)}>
          <fieldset className="container-fluid">
            <legend></legend>
            <div className="row">
              <div className="col-sm-4">
                <div className="form-group">
                  <label></label>
                  <div className="radio"><label><input type="radio" name="size" value="normal" checked={this.state.size === 'normal'} /> (600px)</label></div>
                  <div className="radio"><label><input type="radio" name="size" value="thumb" checked={this.state.size === 'thumb'} /> (150px)</label></div>
                </div>
              </div>

              <div className="col-sm-4">
                <div className="form-group">
                  <label></label>
                  <div className="radio"><label><input type="radio" name="clickAction" value="none" checked={this.state.clickAction === 'none'} />  </label></div>
                  <div className="radio"><label><input type="radio" name="clickAction" value="original" checked={this.state.clickAction === 'original'} /> </label></div>
                  <div className="radio"><label><input type="radio" name="clickAction" value="normal" checked={this.state.clickAction === 'normal'} /> (600px)</label></div>
                  <div></div>
                </div>
              </div>

              <div className="col-sm-4">
                <div className="form-group">
                  <label></label>
                  <div className="checkbox">
                    <label>
                      <input type="checkbox" name="clickRequired" checked={this.state.clickRequired} disabled={this.state.clickAction === 'none'} /> 
                    </label>
                  </div>
                </div>
              </div>
            </div>

            <div className="row">
              <div className="col-sm-12">
                <div className="form-group">
                  <label></label>
                  <input type="text" name="title" className="form-control" />
                </div>
              </div>
            </div>

            <div className="row">
              <div className="col-sm-12">
                <div className="form-group">
                  <label></label>
                  <div className="image-container">
                    {
                      imageList.map((img) => {
                        const isSelected = image && image.id === img.id;
                        return (
                          <div key={img.id} className="image-block">
                            <a
                              className={classNames('image-link', { selected: isSelected })}
                              href="javascript:void(0)"
                              onClick={() => this.handleClickImage(img)}
                            >
                              <img src={img.thumbnailUrl} alt={img.title} />
                            </a>
                            <Button bsStyle="danger" bsSize="xs" className="pull-left" onClick={() => this.handleClickDelete(img)}></Button>
                          </div>
                        );
                      })
                    }
                  </div>
                </div>
              </div>
            </div>
          </fieldset>
        </form>
      </div>
    );
  }
}



